.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.pokedex {
  display: grid;
  width: 100%;
  justify-items: center;
}

.pokemon-list {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  gap: 1em;
}

.poke-card {
  width: 200px;
  display: grid;
  justify-items: center;
  border: solid 2px red;
  border-radius: 10px;
  padding: 1em;
}

nav.nav-bar {
  display: flex;
  justify-content: space-evenly;
}